<template>
  <div class="KedjaHeader">

    <kedja-logo></kedja-logo>

    <div class="slot">
      <slot>
      </slot>
    </div>

    <div>
      <div v-if="auth">
        <drop-down :items="[{label: 'Min profil'},{label: 'Mina väggar'},{label: 'Logga ut', f: logout}]">
          {{auth}}
        </drop-down>
      </div>
      <div v-else>
        <router-link :to="{ name: 'Login'}">Logga in</router-link>
      </div>

    </div>

  </div>
</template>

<script>

import { store } from '@/store';
import KedjaLogo from '@/components/KedjaLogo'
import DropDown from '@/components/DropDown'

export default {
  name: 'KedjaHeader',
  components: {
    KedjaLogo,
    DropDown
  },
  data () {
    return {
    }
  },
  props: {
  },
  computed: {
    auth: function () {
      return store.getters.getAuth;
    }
  },
  methods: {
    logout: function (e) {
      store.commit('resetAuth');
      this.$router.push({ name: 'Login'})
    }
  },
  mounted: function () {
  }
}
</script>

<style scoped>

.KedjaHeader{
  display: flex;
  flex-direction: row;
}

.slot{
  flex: 1;
}

</style>



