<template>
  <div class="ViewLogin">

    <kedja-header>
    </kedja-header>

    <div class="viewContent">
      <h1>
        KEDJA.
      </h1>

      <form @submit.prevent="login">
        <input name="email" type="email" v-model="email" placeholder="E-mail..." required/>
        <input name="password" type="password" v-model="pass" placeholder="Lösenord..." required/>
        <button>Logga in</button>
      </form>
    </div>

  </div>
</template>

<script>

import { store } from '@/store';
import KedjaHeader from '@/components/layout/KedjaHeader'
//import Component from '@/components/Component'

export default {
  name: 'ViewLogin',
  components: {
    KedjaHeader
  },
  data () {
    return {
      email: "",
      pass: ""
    }
  },
  props: {
  },
  computed: {
    auth: function () {
      return store.getters.getAuth;
    }
  },
  methods: {
    login: function (e) {
      store.commit('setAuth', {auth: this.email});
      this.$router.push({ name: 'LoggedIn'})
    },
    loggedIn: function () {
      if(this.auth){
        this.$router.push({ name: 'LoggedIn'})
      }
    }
  },
  created: function () {
    this.loggedIn();
  },
  mounted: function () {
  }
}
</script>

<style scoped>

.ViewLogin{
  /* LAYOUT */
  display: flex;
  flex-direction: column;
}

.viewContent{

  flex: 1;

  display: flex;
  flex-direction: column;

  align-items: center;
  justify-content: center;

}

form{
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

form * {
    margin-bottom: 10px;
}

</style>



